import { CSSInterpolation, serializeStyles } from '@emotion/serialize';
import { BuilderReturnType } from '../types';

const FastGalleryContainerSelector = 'container';
const FastGalleryItemSelector = 'item';
const FastGalleryImageSelector = 'image';

export const containerSelector = ` .${FastGalleryContainerSelector}`;

export const itemSelector = ` .${FastGalleryItemSelector}`;

export const imageSelector = ` .${FastGalleryImageSelector}`;

const cssToJsObject = (cssString: string) => {
  const cssObject: Record<string, any> = {};
  const cssRules = cssString.match(/([^{]+)\{([^}]*)\}/g) || [];

  cssRules.forEach((rule) => {
    const [selector, properties] = rule.split('{');
    const propertyPairs = properties
      .replace('}', '')
      .trim()
      .split(';')
      .filter(Boolean);

    const styleObject = propertyPairs.reduce(
      (acc: Record<string, any>, pair) => {
        const [property, value] = pair.split(':');
        if (property && value) {
          acc[property.trim()] = value.trim();
        }
        return acc;
      },
      {},
    );
    cssObject[selector.trim()] = styleObject;
  });

  return cssObject;
};
export const cssStringToBuilderReturnType = (
  cssString: string,
): BuilderReturnType => {
  return { cssString, cssObject: cssToJsObject(cssString) };
};

export const resetContainerCustomStyleSheet = (
  selector: string,
  additionalCss?: CSSInterpolation,
): BuilderReturnType => {
  const cssReset = serializeStyles([{
    [selector]: {
      display: 'block',
      gridTemplateColumns: 'unset',
      gridTemplateRows: 'unset',
      gridAutoFlow: 'unset',
      gridAutoColumns: 'unset',
      gridAutoRows: 'unset',
      rowGap: 'unset',
      flexWrap: 'nowrap',
      scrollSnapType: 'none',
      overflow: 'visible',
      scrollbarWidth: 'auto',
      gap: 0,
      height: 'auto',
      width: 'auto',
      columnGap: 'normal',
      ...(additionalCss as object),
    },
  }]).styles;
  return cssStringToBuilderReturnType(cssReset);
};

export const resetItemCustomStyleSheet = (
  selector: string,
  additionalCss?: CSSInterpolation,
): BuilderReturnType => {
  const cssReset = serializeStyles([{
    [selector]: {
      gridColumn: 'auto',
      gridRow: 'auto',
      height: 'auto',
      width: 'auto',
      flex: 'unset',
      margin: 0,
      scrollSnapAlign: 'none',
      marginRight: 0,
      ...(additionalCss as object),
    },
    [`${selector}:nth-child(n)`]: {
      gridColumn: 'auto',
      gridRow: 'auto',
      height: 'auto',
      width: 'auto',
      flex: 'unset',
      margin: 0,
      scrollSnapAlign: 'none',
      marginRight: 0,
      ...(additionalCss as object),
    },
  }]).styles;
  return cssStringToBuilderReturnType(cssReset);
};

export const resetImageCustomStyleSheet = (
  selector: string,
  additionalCss?: CSSInterpolation,
): BuilderReturnType => {
  const cssReset = serializeStyles([{
    [selector]: {
      display: 'block',
      width: 'auto !important',
      height: 'auto !important',
      img: {
        width: 'auto !important',
        height: 'auto !important',
        objectFit: 'fill',
        verticalAlign: 'baseline !important',
      },
      ...(additionalCss as object),
    },
  }]).styles;
  return cssStringToBuilderReturnType(cssReset);
};
